.cartscreen {
    display: flex;
    max-width: 1300px;
    margin: 2rem auto;
}
.cartscreen h2 {
    margin-bottom: 1rem;
} 
.cartscreenLeft {
    flex: 0.7;
    margin-right: 1rem;
    background: transparent;
    padding: 1rem;
} 
.cartscreenRight {
    flex: 0.3;
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.4);
    height: fit-content;
}
.cartscreenRight div {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    padding: 1rem;
}
.cartscreenRight div:last-child {
    border: none;
}
.cartscreenInfo p {
    padding: 8px;
  }  
.cartscreenInfo p:first-child {
    font-weight: bold;
}
.cartscreenRight div button {
    padding: 10px 17px;
    width: 100%;
    background: #171717;
    color: #f4f4f4;
    border: 1px solid #171717;
    cursor: pointer;
}
.cartscreenRight div button:hover {
    opacity: 0.9;
}
@media (max-width: 1320px) {
    .cartscreen {
      max-width: 900px;
    }
  }
  
@media (max-width: 960px) {
    .cartscreen {
      max-width: 800px;
    }
}
  
@media (max-width: 960px) {
    .cartscreen {
      flex-direction: column;
    }
  
    .cartscreenRight {
      margin: 1rem;
    }
}